<template>
  <div class="takslist">
    <Top gorouter="/tasklist/working"></Top>
    <div class="workstatus">
      <p class="title">{{status}}</p>
    </div>
    <div class="tasklist">
      <ul>
        <li class="must taskname">
          <span class="name">任务名称：</span>
          <span>{{taskname}}</span>
        </li>
        <li
          v-for="(item,index) in tasklist_add"
          :key="item.id"
          @click="fnuploadimgs(index)"
          class="must upload"
        >
          <van-uploader :after-read="onRead" multiple>
            <slot></slot>
            <template>
              <div class="taskphoto">
                <span class="name">活动图片{{index|plus}}：</span>
                <span class="up">点击上传</span>
              </div>
            </template>
          </van-uploader>
          <ul class="previewimgs" v-if="item.imgs">
            <li
              v-for="itemson in item.imgs"
              :key="itemson"
              :style="{background:'url('+itemson+') center no-repeat',backgroundSize: 'cover'}"
              style="width:80px;height:80px;"
              @click='fnshowimgs(index)'
            ></li>
          </ul>
        </li>
        <li class="must">
          <van-cell-group>
            <van-field
              v-model="message"
              label="留言"
              type="textarea"
              placeholder="请输入留言"
              rows="1"
              autosize
            />
          </van-cell-group>
        </li>
      </ul>
    </div>
    <div class="commit">
      <van-button type="info" size="large" class="login-button bg-blue" @click="fncommit">提交</van-button>
    </div>
  </div>
</template>
<script>
import Top from "@/components/public/top";
import Vue from "vue";
import { ImagePreview } from 'vant';
Vue.use(ImagePreview);
export default {
  data() {
    return {
      taskname: "播种",
      message: "",
      status: "填写农事任务",
      tasklist_add: [{ id: 1, imgs: [] }, { id: 2, imgs: [] }],
      changenum: "",
      obj: [{ a: 1, p: 11 }, { a: 2, p: 22 }]
    };
  },
  filters: {
    plus(v) {
      return v + 1;
    }
  },
  created() {},
  computed: {
    gettasklist_add() {
      for (let i = 0; i < this.carts.length; i++) {
        this.carts[i] = Object.assign({}, this.carts[i], { editState: false });
      }
      this.carts = Object.assign({}, this.carts);
      return this.carts;
    }
  },
  methods: {
    fnuploadimgs(index) {
      this.changenum = index;
    },
    fncommit(){},
    fnshowimgs(index){
      let imgs = this.tasklist_add[index].imgs
      ImagePreview([...imgs]);
    },
    onRead(file) {
      for (let i = 0; i < this.tasklist_add.length; i++) {
        if (i == this.changenum) {
          if (file instanceof Array) {
            for (let item of file) {
              this.tasklist_add[i].imgs.push(item.content);
            }
          } else {
            this.tasklist_add[i].imgs.push(file.content);
          }
        }
      }
    }
  },
  components: {
    Top
  }
};
</script>
<style scoped>
.tasklist {
  padding: 10px;
}
.workstatus {
  background: #0078ad;
  padding: 10px 0;
  color: white;
  text-align: center;
}
.workstatus .title {
  font-size: 10px;
  line-height: 10px;
}
.tasklist .previewimgs {
  display: flex;
  flex-wrap: wrap;
  /* margin-top: -5px; */
  margin-left: -5px;
  margin-right: -5px;
}
.tasklist .previewimgs li {
  width: 50px;
  height: 50px;
  margin: 5px;
}
.tasklist .upload {
  background: white;
  line-height: 44px;
  box-sizing: border-box;
  padding: 0 15px;
  margin-bottom: 10px;
}
.tasklist .van-uploader {
  display: block;
}
.tasklist .van-uploader .name,.tasklist .taskname .name{
  display: inline-block;
  width: 90px;
}
.tasklist .van-uploader .up {
  color: #999;
}
.tasklist > ul > li {
  position: relative;
  text-align: left;
  font-size: 14px;
  color: #323233;
  line-height: 24px;
}

.tasklist .must::after {
  position: absolute;
  right: 8px;
  top: 14px;
  content: "*";
  color: red;
}
.tasklist .must.upload::after {
  top: 3px;
}
.tasklist .taskname{
  background: white;
  line-height: 44px;
  box-sizing: border-box;
  padding: 0 15px;
  margin-bottom: 10px;
}
.commit {
  padding: 20px;
}
/* .commit .out {
  font-size: 14px;
  background: #0078ad;
  color: white;
  line-height: 50px;
} */
</style>


