//请求登录
export const apiLogin = (params) => {
        return axios.post('/wxlogin/login', params)
    }
    // 查询生产人员任务数量
export const apiGetTaskCount = (producter_id, state) => {
        return axios.get('/tTasks/producterIdCount', {
            params: {
                producter_id,
                state
            }
        })
    }
    // 查询生产人员任务
export const apiGetTaskList = (producter_id, state) => {
    return axios.get('/tTasks/producterIdList', {
        params: {
            producter_id,
            state
        }
    })
}


